import React from 'react';
import ExpandedPropertiesComponent from '../component/ExpandedPropertiesComponent';
import {getItemCustomPropertyList,setItemExtCustomList,GetItemCustomList,InsertItemExtendedProperties } from '../state/action/index';
import { connect } from 'react-redux';
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import { withRouter } from 'react-router-dom';
import moment from 'moment';

const userDetails =
    Cookies.get('userDetails') !== undefined ?
        JSON.parse(CryptoJS.AES.decrypt(Cookies.get('userDetails'), process.env.REACT_APP_CryptojsSecretKey).toString(CryptoJS.enc.Utf8))
        : "";
class ExpandedPropertiesContainer extends React.Component {
        state={
            formFields:[]
        }
        
        componentWillMount() {
        }
        componentDidMount() {
            let data ={
                itemId:this.props.itemId
            }
            this.props.getItemCustomPropertyList(data,true)
            window.addEventListener('scroll',this.handleScroll);
            
            try {
            this.clearMessageIntervalID=setInterval(async () => {
               // this.props.clearMessage();
            },10*1000);
            } catch(e) {
            console.log(e);
            }
        }
        componentWillUnmount() {
            window.removeEventListener('scroll',this.handleScroll);
           // clearInterval(this.clearMessageIntervalID);
        }

        inputListClickHandler=(e,name)=>
        {
           
            let data={
                name:name
            }
            this.props.GetItemCustomList(data,true)
        }
        onChangeHandler=(e,type,name,id)=>
        {
          debugger
            let formFields=this.state.formFields;
            let getCustomList=this.props.getCustomList;

            var _value="";
            if(type==0 || type==5 || type==2 || type==3)
            {
                _value=e.target.value 
            }
            else if(type==4)
            {
                _value=e.target.checked 
            }
            else if(type==1 && (e!==null))
            {
                _value= moment(e).format('YYYY-MM-DD');
            }
            if(formFields.filter(x => x.Name===name).length==0)
            {
                formFields.push({Id: id,Name:name,Value:_value.toString(),Type:type});
            }
            else
            {
                var index1=formFields.findIndex(x=>x.Name==name)
                if (index1 > -1) {
                    formFields[index1].value=_value.toString();
                } 
            }
           
            var index=getCustomList.findIndex(x=>x.name==name)
            if (index > -1) {
                getCustomList[index].value=_value.toString();
            }

            this.props.setItemExtCustomList(getCustomList)
            //}
            this.props.setcurrentPODChanged("Extended Properties");
           
            
            this.setState({formFields})
        }
        //#region post form data to save
        formHandler=()=> {
                try {
                    debugger;
                    let itemExtData={
                        itemExt:this.state.formFields,
                        itemId: this.props.itemId

                    }
                    this.props.InsertItemExtendedProperties(itemExtData,true)
                    
                }catch(error) {
                    console.log(error);
                }
            }
            //#endregion post form data to save
            
        render() {
            return (
                <ExpandedPropertiesComponent
                    {...this.props}
                    inputListClickHandler={this.inputListClickHandler}
                    onChangeHandler={this.onChangeHandler}
                    formFields={this.state.formFields}
                    formHandler={this.formHandler}
                />
            )
        }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getItemCustomPropertyList:(data,isLoading)=>dispatch(getItemCustomPropertyList(data,isLoading)),
        GetItemCustomList:(data,isLoading)=>dispatch(GetItemCustomList(data,isLoading)),
        InsertItemExtendedProperties:(data,isLoading)=>dispatch(InsertItemExtendedProperties(data,isLoading)),
        setItemExtCustomList:(data)=>dispatch(setItemExtCustomList(data))
    };
};

const mapStateToProps = (state) => {
    const {getCustomList,CustomDropList } = state.ExpandedPropertiesReducer;
    return {
        getCustomList:getCustomList,
        CustomDropList:CustomDropList.length>0?CustomDropList:[]

    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ExpandedPropertiesContainer));